<template>
    <div>
        <el-button @click="connectMetaMask" :loading="loading">MetaMask</el-button>
    </div>
</template>

<script>
import * as ethers from 'ethers'
export default {
    data() {
        return {
            loading: false
        }
    },
    methods: {
        connectMetaMask() {
            this.loading = true
            const status = window.ethereum.isConnected()
            window.ethereum.request({
                method: 'eth_requestAccounts'
            }).then((result) => {
                console.log(result)
                const provider = new ethers.providers.Web3Provider(window.ethereum)
                const signer = provider.getSigner()
                console.log(signer)
            }).catch((err) => {
                console.log(err)
            }).finally(() => {
                this.loading = false
            });
        }
    }
}
</script>

